.block-editor {
	.event-attendees {
		 ul {
			list-style-type: none;
		}

		li {
    		align-items: center;
			display: flex;
			flex-flow: row nowrap;
			line-height: 1.2;

			.contact-image-wrap-div {
				display: inline-block;
			}

			/** account for this current problem in WP 5.0 beta: https://core.trac.wordpress.org/ticket/45290 **/
			p {
				display: inline-block;
			}

			.contact-avatar-img {
				margin: 0.25em 1em 0.25em 0;
				vertical-align: middle;
			}
		}
	}

	.event-attendees.font-size-xxs { font-size: x-small; }
	.event-attendees.font-size-xs { font-size: small; }
	.event-attendees.font-size-sm { font-size: medium; }
	.event-attendees.font-size-md { font-size: large; }
	.event-attendees.font-size-lg { font-size: larger; }
	.event-attendees.font-size-xl { font-size: xx-large; }
	.event-attendees.font-size-xxl { font-size: xxx-large; }
}
